.profile {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 350px;
  height: 350px;
  background-color: #ccc;
  border: 1px solid black;
}

.white {
  background-color: #fff;
  width: 250px;
  height: 300px;
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.gray {
  color: gray;
  font-size: 12px;
  margin-bottom: -10px;
}

.avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.userName {
  font-weight: bold;
  font-size: 14px;
  margin-bottom: -10px;
}

.userStatiList {
  display: flex;
  flex-direction: row;
  /* margin-right: 40px; */
  width: 100%;
  margin-top: 40px;
  margin-right: 40px;
  margin-bottom: -35px;
}

.userStat {
  list-style-type: none;
  background-color: lightslategray;
  padding: 10px;
  border: 1px solid #000;
}
