.statistics {
  display: inline-block;
  text-align: center;
  padding: 5px;
  margin-top: 50px;
  background-color: rgb(211, 102, 211);
  color: #000;
}

.title {
  text-align: center;
  font-size: 32px;
  color: white;
  font-weight: bold;
}

.statsList {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  padding: 10px;
}

.statItem {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding: 10px;
  margin: 5px;
}

.red {
  background-color: red;
}

.yellow {
  background-color: yellow;
}

.blue {
  background-color: blue;
}

.brown {
  background-color: brown;
}

.pink {
  background-color: pink;
}

.green {
  background-color: green;
}
