.friendList {
  list-style-type: none;
  display: inline-flex;
  flex-direction: column;
}

.item {
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  margin: 5px;
  min-width: 150px;
  flex-direction: row;
  background-color: rgb(160, 159, 159);
}

.red {
  height: 20px;
  width: 20px;
  background-color: red;
  border-radius: 50%;
}

.green {
  height: 20px;
  width: 20px;
  background-color: green;
  border-radius: 50%;
}
